<template>
   <div style="margin:10px;direction: rtl;">
      <Row style="margin:10px 0">
         <Col style="text-align:right">
            <Button
               type="success"
               shape="circle"
               icon="ios-add"
               @click="addDriver = true"
               >اضافة سائق</Button
            >
         </Col>
      </Row>
      <Table border :columns="columns12" :data="drivers" :loading="loading">
         <template slot-scope="{ row }" slot="name">
            <strong>{{ row.name }}</strong>
         </template>
         <template slot-scope="{ row, index }" slot="action">
            <Button type="error" size="small" @click="remove(row)">حذف</Button>
         </template>
      </Table>

      <Modal
         v-model="addDriver"
         title="اضافة سائق"
         width="600px"
         @on-ok="ok"
         @on-cancel="cancel"
      >
         <Row style="direction:rtl">
            <Col span="24">
               <div>
                  <Input
                     v-model="addNewDriver.driverName"
                     placeholder="اسم السائق..."
                     style="width: 200px;margin : 5px auto"
                  />
               </div>
               <div>
                  <Input
                     v-model="addNewDriver.phone"
                     placeholder=" رقم الهاتف..."
                     style="width: 200px ;margin : 5px auto"
                  />
               </div>

               <div>
                  <Input
                     v-model="addNewDriver.nationalId"
                     placeholder="رقم الهوية..."
                     style="width: 200px ;margin : 5px auto"
                  />
               </div>
               <div>
                  <Input
                     v-model="addNewDriver.license"
                     placeholder="رقم السنوية..."
                     style="width: 200px ;margin : 5px auto"
                  />
               </div>
            </Col>
         </Row>
      </Modal>
   </div>
</template>

<script>
export default {
   data() {
      return {
         columns12: [
            {
               title: "اسم السائق",
               key: "driverName",
            },
            {
               title: "رقم الهاتف",
               key: "phone",
            },
            {
               title: "رقم السنوية",
               key: "license",
            },
            {
               title: "رقم هوية الاحوال المدنية",
               key: "nationalId",
            },
            {
               title: "Action",
               slot: "action",
               width: 150,
               align: "center",
            },
         ],
         drivers: [],
         loading: true,
         addDriver: false,
         addNewDriver: {
            driverName: "",
            phone: "",
            nationalId: "",
            license: "",
         },
      };
   },
   created() {
      this.getDrivers();
   },
   methods: {
      getDrivers() {
         let self = this;
         self.loading = true;
         this.$http
            .get("http://invoice.srit.me/drivers")
            .then((res) => {
               console.log(res.data);
               self.drivers = res.data;
               self.loading = false;
            })
            .catch((e) => {
               console.log(e);
            });
      },
      remove(index) {
         let self = this;
         console.log(index);
         this.$http
            .delete(`http://invoice.srit.me/deleteDriver/${index.idDriver}`)
            .then((res) => {
               self.$Message["success"]({
                  background: true,
                  content: "تم حذف السائق",
               });
               this.getDrivers();
            })
            .catch((e) => {
               self.$Message["error"]({
                  background: true,
                  content: "حدث خطأ لم يتم حذف السائق",
               });
            });
      },
      ok() {
         let self = this;
         if (
            this.addNewDriver.driverName &&
            this.addNewDriver.phone &&
            this.addNewDriver.license &&
            this.addNewDriver.nationalId
         ) {
            this.$http
               .post("http://invoice.srit.me/addDriver", this.addNewDriver)
               .then((res) => {
                  self.$Message["success"]({
                     background: true,
                     content: "تم حفظ البيانات",
                  });
                  this.getDrivers();
               })
               .catch((e) => {
                  self.$Message["error"]({
                     background: true,
                     content: "حدث خطأ اثناء حفظ البيانات تأكد من المعلومات",
                  });
               });
         } else {
            self.$Message["error"]({
               background: true,
               content: "حدث خطأ اثناء حفظ البيانات تأكد من المعلومات",
            });
         }
      },
      cancel() {
         this.$Message["info"]({
            background: true,
            content: "تم الغاء الطلب",
         });
      },
   },
};
</script>

<style lang="scss" scoped></style>
